  position: relative;
  top: 45%;
}

.double-bounce1, .double-bounce2 {
  opacity: .6;
  background-color: #f97794;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  animation: 2s ease-in-out infinite sk-bounce;
  position: absolute;
  top: 0;
  left: 0;
}

.double-bounce2 {
  animation-delay: -1s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}

